.navItems{
    display: none;
}

.brand{
    display: inline-flex;
    flex-wrap: nowrap;
    align-items: center;
    gap: 0;
    padding-left: 32px;
}

.navLogo{
    width: 24px;
    height: 24px;
}

.lgText{
    font-size: 8px;
    font-weight: 500;
}

.harmBurger{
    padding-right: 36px;
}

.hamburgerImg{
    width: 19.5px;
    height: 11.5px;
}
.sss{
    z-index: 10;
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
  
}
.harmBurgerDropDown{

    padding-left: 38px;
    background-color: white;
    transition: margin 0.5s ease-in;
}

.harmBurgerDropDown ul li{
    list-style: none;
    font-size: 24px;
    font-weight: 500;
    padding-bottom: 48px;
}

.hamBurgerActive{
    padding-top: 48px;
}

/*Footer*/

footer{
    position: fixed;
    bottom: 0;
    left: 0;
    right: 0;
    background: black;
    padding:24px 34px 24px 0;
    color: white;
    display: inline-flex;
    flex-direction: row;
    gap: 35px;
    width: 100%;
    justify-content: center;
    /*z-index: -1000000;*/
}

footer h1{
    font-weight: 500;
    font-size: 12px;
    line-height: 15px;
     letter-spacing: 0.5px; 
}

footer  ul li{
    list-style: none;
    font-size: 8px;
    font-weight: 400;
    line-height: 24px;
     letter-spacing: 0.5px;
}

footer ul{
    padding: 0;
}

.footerBrand {
    display: inline-flex;
    align-items: center;
    flex-wrap: nowrap;
    padding-left: 25px;
    justify-content: flex-start;
}

.footerLogo{
    width: 30px;
    height: 30px;
}

.footerlgText{
    font-size: 8px;
    font-weight: 500;
    letter-spacing: 0.5px;
}

.socialSeg, .companySeg{
    padding-left: 34px;
}

.socialHead, .companyHead, .menuHead, .offersHead{
    margin-bottom: 10px;
}

.companySeg ul{
    margin-bottom: 15px;
}

