.socialSeg2{
    display: none;
}

ul{
    padding: 0;
}

/*main body*/
.heroImg{
    margin-top: 18px;
}

.heroImg img{
    width: 238.26px;
    height: 357.28px;
}

.hero{
    margin-top: 40px;
    background: #F9B70B;
    padding: 40px 36px 83px 36px;
}

.hero2{
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.heroText{
    font-weight: 500;
    font-size: 26px;
    line-height: 39px;
    text-align: left;
}

.whatDoYouWantcontainer {
    margin: 0 auto;
    background: black;
    color: white;
    border-radius: 5px;
    padding: 12px;
    width: 229px;
    height: 91px;
    position: absolute;
    left: 0;
    right: 0;
}

.wantInput input {
    border: none;
    margin-top: 3px;
    width: 187px;
    height: 12px;
    font-size: 10px;
    text-indent: 5px;
}

.wantInput input:active {
    outline: white;
    border: white;
}

.wantInput input:focus{
    border: none;
}

.wantInput input:focus-visible{
    border: 0 none white;
    background: white;
    outline: none;
}

.testRel{
    margin: 33px auto;
    position: relative;
}


.searchBtn {
    margin-top: 1px;
    height: 30px;
    width: 30px;
}

.wantInput {
   padding: 2px;
    display: inline-flex;
    width: 227px;
    height: 20px;
    background: #FFFFFF;
    border-radius: 2px;
    align-items: stretch;
    justify-content: space-between;
}

.wantText{
    font-weight: 500;
    font-size: 16px;
    margin-bottom: 10px;
}

.wantTrend {
    font-family: Poppins;
    margin-top: 13px;
    padding-left: 5px;
}

.wantTrend p{
    font-family: Poppins;
    font-size: 4px;
}

.wantTrend p u{
    margin-left: 4px;
    color: #F9B70B;
}
.coursesContainer{
    margin: 0 auto;
    padding-top: 60px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    gap: 36px;
}
.feaCourses{
    display: inline-flex;
    flex-direction: row;
    gap: 23px;
    justify-content: center;
    align-items: center;
}

.headText{
    font-style: normal;
    font-weight: 500;
    font-size: 12px;
    line-height: 20px;
    color: #F9B70B;
}

.subText{
    font-family: 'Poppins';
    font-style: normal;
    font-weight: 400;
    font-size: 7px;
    line-height: 12px;
    color: #000000;
}

.viewCouses button{
    font-family: 'clash display';
    width: 64.57px;
    height: 24px;
    background: #000000;
    color: white;
    border: none;
    font-size: 8px;
    border-radius: 5px;
}

.courcesContainer2{
    display: flex;
    gap: 61px;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

